<template>
  <div class="loader">
   
  </div>
</template>

<script>
export default {
   name: 'my-loader'
}
</script>
<style lang="scss" scoped>
.loader {
   width : 50px;
    height: 50px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 3px solid;
    border-color: var(--color-green) var(--color-orange) transparent;
    animation: rotation 1s linear infinite;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      border: 3px solid;
      border-color: transparent var(--color-green) var(--color-orange) ;
      width:  25px;
      height:  25px;
      border-radius: 50%;
      animation: rotationBack 0.5s linear infinite;
      transform-origin: center center;
    }
}
@keyframes rotation {
    0% { transform: rotate(0deg) }
    100% { transform: rotate(360deg) }
  }
@keyframes rotationBack {
   0% { transform: rotate(0deg) }
   100% { transform: rotate(-360deg) }
}
</style>